import * as React from "react";
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import { styled } from "@mui/material/styles";
import AutoFixHighIcon from "@mui/icons-material/AutoFixHigh";
import AdamDarkMode from "../adam-dark-mode.svg";
import AdamWhiteMode from "../adam-white-mode.svg";
import { useTheme } from "@emotion/react";
import { useMediaQuery } from "@mui/material";
const TypewriterText = styled(Typography)(({ theme }) => ({
  color: theme.palette.text.primary,

  fontFamily: "monospace",
  "&.typing": {
    borderRight: "2px solid",
    animation: "blink 0.7s infinite",
  },
  "@keyframes blink": {
    "50%": {
      borderColor: "transparent",
    },
  },
}));

const demoTexts = [
  {
    ai: "בוצעה אופטימיזציה של המערכת להשגת ביצועים אופטימליים.",
    human: "שיפרנו את המערכת כדי שתעבוד הרבה יותר טוב.",
  },
  {
    ai: "הפלטפורמה מציגה שילוב של פונקציונליות והנדסת אנוש.",
    human: "המערכת קלה לשימוש ויש בה את כל מה שצריך.",
  },
  {
    ai: "התקבלו תוצאות חיוביות בכל הפרמטרים התפעוליים.",
    human: "קיבלנו תוצאות מעולות בכל המדדים!",
  },
];

export default function Hero() {
  const [currentText, setCurrentText] = React.useState({ ai: "", human: "" });
  const [currentChar, setCurrentChar] = React.useState(0);
  const [currentIndex, setCurrentIndex] = React.useState(0);
  const [isTyping, setIsTyping] = React.useState(true);
  const theme = useTheme();
  const isMobile = useMediaQuery("(max-width:1000px)");
  React.useEffect(() => {
    const text = demoTexts[currentIndex];
    const currentString = isTyping ? text.ai : text.human;

    if (currentChar < currentString.length) {
      const timeout = setTimeout(() => {
        setCurrentText((prev) => ({
          ...prev,
          [isTyping ? "ai" : "human"]: currentString.slice(0, currentChar + 1),
        }));
        setCurrentChar((prev) => prev + 1);
      }, 50);

      return () => clearTimeout(timeout);
    } else {
      const timeout = setTimeout(() => {
        if (isTyping) {
          setCurrentChar(0);
          setIsTyping(false);
        } else {
          setTimeout(() => {
            setCurrentText({ ai: "", human: "" });
            setCurrentChar(0);
            setCurrentIndex((prev) => (prev + 1) % demoTexts.length);
            setIsTyping(true);
          }, 1500);
        }
      }, 1000);

      return () => clearTimeout(timeout);
    }
  }, [currentChar, currentIndex, isTyping]);

  return (
    <Container maxWidth="md" sx={{ height: "40vh", mt: 12 }}>
      <Stack spacing={6} alignItems="center">
        <Stack spacing={3} alignItems="center">
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              gap: 2,
            }}
          >
            {/* <AutoFixHighIcon
              sx={{
                fontSize: 32,
                color: "primary.main",
              }}
            /> */}
            {theme.palette.mode === "dark" ? (
              <img
                src={AdamDarkMode}
                alt="Adam AI"
                style={{ width: isMobile ? "100px" : "200px" }}
              />
            ) : (
              <img
                src={AdamWhiteMode}
                alt="Adam AI"
                style={{ width: isMobile ? "100px" : "200px" }}
              />
            )}
          </Box>
          <Typography variant="h6" color="text.secondary" textAlign="center">
            הפוך טקסט AI לאנושי בלחיצת כפתור
          </Typography>
        </Stack>

        <Box
          sx={{
            width: "100%",
            minHeight: 160,
            display: "flex",
            flexDirection: "column",
            gap: 3,
          }}
        >
          <Box
            sx={{
              p: 3,
              borderRadius: 2,
              bgcolor: (theme) =>
                theme.palette.mode === "dark"
                  ? "rgba(99, 102, 241, 0.1)"
                  : "rgba(79, 70, 229, 0.1)",
              border: "1px solid",
              borderColor: (theme) =>
                theme.palette.mode === "dark"
                  ? "rgba(99, 102, 241, 0.2)"
                  : "rgba(79, 70, 229, 0.2)",
            }}
          >
            <TypewriterText
              variant="body1"
              className={isTyping ? "typing" : ""}
              sx={{
                fontSize: "1.1rem",
                direction: "ltr",
                minHeight: "28px",
              }}
            >
              {currentText.ai}
            </TypewriterText>
          </Box>

          <Box
            sx={{
              p: 3,
              borderRadius: 2,
              bgcolor: (theme) =>
                theme.palette.mode === "dark"
                  ? "rgba(34, 197, 94, 0.1)"
                  : "rgba(22, 163, 74, 0.1)",
              border: "1px solid",
              borderColor: (theme) =>
                theme.palette.mode === "dark"
                  ? "rgba(34, 197, 94, 0.2)"
                  : "rgba(22, 163, 74, 0.2)",
            }}
          >
            <TypewriterText
              variant="body1"
              className={!isTyping ? "typing" : ""}
              sx={{
                fontSize: "1.1rem",
                direction: "ltr",
                minHeight: "28px",
              }}
            >
              {currentText.human}
            </TypewriterText>
          </Box>
        </Box>
      </Stack>
    </Container>
  );
}
