import * as React from "react";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormLabel from "@mui/material/FormLabel";
import Grid from "@mui/material/Grid2";
import OutlinedInput from "@mui/material/OutlinedInput";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import { styled } from "@mui/system";

const FormGrid = styled(Grid)(() => ({
  display: "flex",
  flexDirection: "column",
  "& .MuiOutlinedInput-root": {
    borderRadius: "8px",
  },
}));

export default function RegistrationForm() {
  const [useType, setUseType] = React.useState("personal");

  return (
    <Grid container spacing={3} sx={{ direction: "ltr" }}>
      <FormGrid size={{ xs: 12 }}>
        <FormLabel htmlFor="use-type" required>
          סוג שימוש
        </FormLabel>
        <Select
          id="use-type"
          value={useType}
          onChange={(e) => setUseType(e.target.value)}
          size="small"
        >
          <MenuItem value="personal">שימוש אישי</MenuItem>
          <MenuItem value="business">שימוש עסקי</MenuItem>
          <MenuItem value="academic">שימוש אקדמי</MenuItem>
        </Select>
      </FormGrid>

      <FormGrid size={{ xs: 12, md: 6 }}>
        <FormLabel htmlFor="first-name" required>
          שם פרטי
        </FormLabel>
        <OutlinedInput
          id="first-name"
          name="first-name"
          type="text"
          placeholder="ישראל"
          autoComplete="given-name"
          required
          size="small"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12, md: 6 }}>
        <FormLabel htmlFor="last-name" required>
          שם משפחה
        </FormLabel>
        <OutlinedInput
          id="last-name"
          name="last-name"
          type="text"
          placeholder="ישראלי"
          autoComplete="family-name"
          required
          size="small"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12 }}>
        <FormLabel htmlFor="email" required>
          כתובת אימייל
        </FormLabel>
        <OutlinedInput
          id="email"
          name="email"
          type="email"
          placeholder="your@email.com"
          autoComplete="email"
          required
          size="small"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12 }}>
        <FormLabel htmlFor="organization">
          ארגון {useType === "personal" ? "(אופציונלי)" : ""}
        </FormLabel>
        <OutlinedInput
          id="organization"
          name="organization"
          type="text"
          placeholder="שם הארגון או החברה"
          required={useType !== "personal"}
          size="small"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12, md: 6 }}>
        <FormLabel htmlFor="phone" required>
          טלפון
        </FormLabel>
        <OutlinedInput
          id="phone"
          name="phone"
          type="tel"
          placeholder="050-0000000"
          autoComplete="tel"
          required
          size="small"
        />
      </FormGrid>

      {useType === "business" && (
        <FormGrid size={{ xs: 12, md: 6 }}>
          <FormLabel htmlFor="company-id" required>
            ח.פ / עוסק מורשה
          </FormLabel>
          <OutlinedInput
            id="company-id"
            name="company-id"
            type="text"
            placeholder="מספר ח.פ או עוסק מורשה"
            required
            size="small"
          />
        </FormGrid>
      )}

      {useType === "academic" && (
        <FormGrid size={{ xs: 12, md: 6 }}>
          <FormLabel htmlFor="institution" required>
            מוסד אקדמי
          </FormLabel>
          <Select id="institution" size="small" defaultValue="">
            <MenuItem value="">בחר מוסד</MenuItem>
            <MenuItem value="tau">אוניברסיטת תל אביב</MenuItem>
            <MenuItem value="huji">האוניברסיטה העברית</MenuItem>
            <MenuItem value="technion">הטכניון</MenuItem>
            <MenuItem value="bgu">אוניברסיטת בן גוריון</MenuItem>
            <MenuItem value="haifa">אוניברסיטת חיפה</MenuItem>
            <MenuItem value="other">אחר</MenuItem>
          </Select>
        </FormGrid>
      )}

      <FormGrid size={{ xs: 12 }}>
        <FormControlLabel
          control={<Checkbox name="newsletter" value="yes" defaultChecked />}
          label="אני מעוניין לקבל עדכונים ומידע על מוצרים חדשים"
        />
      </FormGrid>

      <FormGrid size={{ xs: 12 }}>
        <FormControlLabel
          control={<Checkbox name="terms" value="yes" required />}
          label={
            <span>
              קראתי ואני מסכים ל
              <a
                href="/terms"
                style={{
                  color: "primary",
                  textDecoration: "underline",
                  margin: "0 4px",
                }}
              >
                תנאי השימוש
              </a>
              ול
              <a
                href="/privacy"
                style={{
                  color: "primary",
                  textDecoration: "underline",
                  margin: "0 4px",
                }}
              >
                מדיניות הפרטיות
              </a>
            </span>
          }
        />
      </FormGrid>
    </Grid>
  );
}
