import * as React from "react";
import PropTypes from "prop-types";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Card from "@mui/material/Card";
import MuiChip from "@mui/material/Chip";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";

import { alpha, styled } from "@mui/material/styles";

import DevicesRoundedIcon from "@mui/icons-material/DevicesRounded";
import EdgesensorHighRoundedIcon from "@mui/icons-material/EdgesensorHighRounded";
import ViewQuiltRoundedIcon from "@mui/icons-material/ViewQuiltRounded";
import AutoFixHighIcon from "@mui/icons-material/AutoFixHigh";
import SpeedIcon from "@mui/icons-material/Speed";
import SecurityIcon from "@mui/icons-material/Security";
import Future1 from "../assest/Future1.png";
import Future2 from "../assest/Future2.png";
import Future3 from "../assest/Future3.png";
import Future4 from "../assest/Future4.png";
import Future5 from "../assest/Future5.png";
const items = [
  {
    icon: <ViewQuiltRoundedIcon />,
    title: "טקסט טבעי ואנושי",
    description:
      "ADAM הופך טקסט שנוצר על ידי AI לטקסט טבעי ואותנטי שנשמע כמו כתיבה אנושית אמיתית. המערכת מזהה ומתקנת דפוסים מלאכותיים ומייצרת תוכן שקשה להבדיל בינו לבין כתיבה אנושית.",
    imageLight: Future1,
    imageDark: Future1,
  },
  {
    icon: <EdgesensorHighRoundedIcon />,
    title: "התאמה אישית מתקדמת",
    description:
      "התאימו את הסגנון, הטון והמורכבות של הטקסט לצרכים שלכם. ADAM מאפשר שליטה מלאה על אופי הטקסט - מפורמלי ועד קליל, מפשוט ועד מורכב, הכל בהתאמה למותג ולקהל היעד שלכם.",
    imageLight: Future2,
    imageDark: Future2,
  },

  {
    icon: <AutoFixHighIcon />,
    title: "זיהוי וניתוח חכם",
    description:
      "מערכת AI מתקדמת המזהה ומנתחת מאפיינים של טקסט מלאכותי. ADAM לומד ומשתפר באופן מתמיד, מתאים את עצמו לשינויים בטכנולוגיות AI ומבטיח תוצאות מיטביות.",
    imageLight: Future3,
    imageDark: Future3,
  },
  {
    icon: <SpeedIcon />,
    title: "מהירות ויעילות",
    description:
      "עיבוד מהיר ויעיל של טקסטים בכל היקף. ADAM מסוגל לטפל בכמויות גדולות של תוכן במהירות, תוך שמירה על איכות גבוהה ועקביות.",
    imageLight: Future4,
    imageDark: Future4,
  },
  {
    icon: <SecurityIcon />,
    title: "אבטחה ופרטיות",
    description:
      "אבטחת מידע ברמה הגבוהה ביותר. כל הטקסטים מעובדים בסביבה מאובטחת, עם הצפנה מלאה ושמירה קפדנית על פרטיות המשתמשים.",
    imageLight: Future5,
    imageDark: Future5,
  },
];
const Chip = styled(MuiChip)(({ theme }) => ({
  variants: [
    {
      props: ({ selected }) => selected,
      style: {
        background:
          "linear-gradient(to bottom right, hsl(210, 98%, 48%), hsl(210, 98%, 35%))",
        color: "hsl(0, 0%, 100%)",
        borderColor: (theme.vars || theme).palette.primary.light,
        "& .MuiChip-label": {
          color: "hsl(0, 0%, 100%)",
        },
        ...theme.applyStyles("dark", {
          borderColor: (theme.vars || theme).palette.primary.dark,
        }),
      },
    },
  ],
}));

function MobileLayout({ selectedItemIndex, handleItemClick, selectedFeature }) {
  if (!items[selectedItemIndex]) {
    return null;
  }

  return (
    <Box
      sx={{
        display: { xs: "flex", sm: "none" },
        flexDirection: "column",
        gap: 2,
      }}
    >
      <Box sx={{ display: "flex", gap: 2, overflow: "auto" }}>
        {items.map(({ title }, index) => (
          <Chip
            size="medium"
            key={index}
            label={title}
            onClick={() => handleItemClick(index)}
            selected={selectedItemIndex === index}
          />
        ))}
      </Box>
      <Card variant="outlined">
        <Box
          sx={(theme) => ({
            mb: 2,
            backgroundSize: "cover",
            backgroundPosition: "center",
            minHeight: 280,
            backgroundColor: theme.vars
              ? `rgba(${theme.vars.palette.background.defaultChannel} / 0.4)`
              : alpha(theme.palette.background.default, 0.4),
          })}
          style={{
            backgroundImage: `url(${items[selectedItemIndex].imageLight})`,
          }}
        />
        <Box sx={{ px: 2, pb: 2 }}>
          <Typography
            gutterBottom
            sx={{ color: "text.primary", fontWeight: "medium" }}
          >
            {selectedFeature.title}
          </Typography>
          <Typography variant="body2" sx={{ color: "text.secondary", mb: 1.5 }}>
            {selectedFeature.description}
          </Typography>
        </Box>
      </Card>
    </Box>
  );
}

MobileLayout.propTypes = {
  handleItemClick: PropTypes.func.isRequired,
  selectedFeature: PropTypes.shape({
    description: PropTypes.string.isRequired,
    icon: PropTypes.element,
    imageDark: PropTypes.string.isRequired,
    imageLight: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
  }).isRequired,
  selectedItemIndex: PropTypes.number.isRequired,
};

export { MobileLayout };

export default function Features() {
  const [selectedItemIndex, setSelectedItemIndex] = React.useState(0);

  const handleItemClick = (index) => {
    setSelectedItemIndex(index);
  };

  const selectedFeature = items[selectedItemIndex];

  return (
    <Container id="features" sx={{ py: { xs: 8, sm: 16 } }}>
      <Box sx={{ width: { sm: "100%", md: "60%" } }}>
        <Typography
          component="h2"
          variant="h4"
          gutterBottom
          textAlign={{ xs: "center", sm: "left" }}
          sx={{ color: "text.primary" }}
        >
          למה ADAM?
        </Typography>
        <Typography
          variant="body1"
          textAlign={{ xs: "center", sm: "left" }}
          sx={{ color: "text.secondary", mb: { xs: 2, sm: 4 } }}
        >
          ADAM הוא פתרון מתקדם להפיכת טקסט AI לתוכן אנושי ואותנטי. באמצעות
          טכנולוגיית AI מתקדמת, ADAM מזהה ומשפר טקסטים שנוצרו על ידי בינה
          מלאכותית, הופך אותם לטבעיים יותר ומותאמים יותר לקריאה אנושית.
        </Typography>
      </Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: { xs: "column", md: "row-reverse" },
          gap: 2,
        }}
      >
        <div>
          <Box
            sx={{
              display: { xs: "none", sm: "flex" },
              flexDirection: "column",
              gap: 2,
              height: "100%",
            }}
          >
            {items.map(({ icon, title, description }, index) => (
              <Box
                key={index}
                component={Button}
                onClick={() => handleItemClick(index)}
                sx={[
                  (theme) => ({
                    p: 2,
                    height: "100%",
                    width: "100%",
                    "&:hover": {
                      backgroundColor: (theme.vars || theme).palette.action
                        .hover,
                    },
                  }),
                  selectedItemIndex === index && {
                    backgroundColor: "action.selected",
                  },
                ]}
              >
                <Box
                  sx={[
                    {
                      width: "100%",
                      display: "flex",
                      flexDirection: "column",
                      alignItems: "left",
                      gap: 1,
                      textAlign: "left",
                      textTransform: "none",
                      color: "text.secondary",
                    },
                    selectedItemIndex === index && {
                      color: "text.primary",
                    },
                  ]}
                >
                  {icon}

                  <Typography variant="h6">{title}</Typography>
                  <Typography variant="body2">{description}</Typography>
                </Box>
              </Box>
            ))}
          </Box>
          <MobileLayout
            selectedItemIndex={selectedItemIndex}
            handleItemClick={handleItemClick}
            selectedFeature={selectedFeature}
          />
        </div>
        <Box
          sx={{
            display: { xs: "none", sm: "flex" },
            width: { xs: "100%", md: "70%" },
            height: "var(--items-image-height)",
          }}
        >
          <Card
            variant="outlined"
            sx={{
              height: "100%",
              width: "100%",
              display: { xs: "none", sm: "flex" },
              pointerEvents: "none",
            }}
          >
            <Box
              sx={(theme) => ({
                m: "auto",
                width: 420,
                height: 500,
                backgroundSize: "contain",
                backgroundImage: "var(--items-imageLight)",
                ...theme.applyStyles("dark", {
                  backgroundImage: "var(--items-imageDark)",
                }),
              })}
              style={{
                backgroundImage: `url(${items[selectedItemIndex].imageLight})`,
                backgroundSize: "contain",
                backgroundPosition: "center",
                backgroundRepeat: "no-repeat",
              }}
            />
          </Card>
        </Box>
      </Box>
    </Container>
  );
}
