import React, { useState, useMemo, useEffect } from "react";
import MenuIcon from "@mui/icons-material/Menu";

import {
  Box,
  Button,
  IconButton,
  Switch,
  Paper,
  Stack,
  Tooltip,
  CircularProgress,
  useMediaQuery,
  Menu,
  MenuItem,
  Tabs,
  Tab,
  Icon,
  Typography,
} from "@mui/material";
import { alpha, styled } from "@mui/material/styles";
import GoalsDialog from "./Adam-Components/GoalsDialog";

import ThumbUpIcon from "@mui/icons-material/ThumbUp";
import ThumbDownIcon from "@mui/icons-material/ThumbDown";
import DownloadIcon from "@mui/icons-material/Download";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import TextArea from "./Adam-Components/TextArea";
import { blue } from "@mui/material/colors";
import { useTheme } from "@emotion/react";
import axios from "axios";
import { AnalyzeText } from "./api";

const ActionButton = styled(Button)(({ theme }) => ({
  color: alpha(theme.palette.text.primary, 1),
  textTransform: "none",
  borderRadius: "8px",
  padding: "6px 16px",
  fontSize: "14px",
  fontFamily: "Assistant, Rubik, Arial, sans-serif",
  border: `1px solid ${theme.vars ? "#0f1117" : "#fff"}`,
  "&:hover": {
    backgroundColor: "rgba(255, 255, 255, 0.05)",
  },
}));

const CustomSwitch = styled(Switch)({
  "& .MuiSwitch-track": {
    backgroundColor: "#1e2029",
  },
});

const StyledPaper = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.vars
    ? theme.vars.palette.background.default
    : "#0f1117",
  color: theme.vars ? theme.vars.palette.background.default : "#0f1117",
  minHeight: "600px",
  display: "flex",
  flexDirection: "column",
  borderRadius: "12px",
  padding: "20px",
  gap: "20px",
  transition: "all 0.3s ease",
  border: "1px solid ",
  borderColor: (theme.vars || theme).palette.divider,
  "&:hover": {
    borderColor: "#2e2f39",
  },
}));

const TopButton = styled(Button)(({ active, theme }) => ({
  color: theme.vars ? "#0f1117" : theme.vars.palette.background.default,
  textTransform: "none",
  fontSize: "14px",
  padding: "8px 16px",
  fontFamily: "Assistant, Rubik, Arial, sans-serif",
  borderRadius: "8px",
  transition: "all 0.2s ease",
  "&:hover": {
    color: theme.vars ? "#0f1117" : theme.vars.palette.background.default,
  },
  ...(active && {
    backgroundColor: "rgba(255, 255, 255, 0.05)",
    "&::after": {
      content: '""',
      position: "absolute",
      bottom: 0,
      left: 0,
      right: 0,
      height: "2px",
      backgroundColor: "#7c3aed",
    },
  }),
}));

const DropdownMenu = ({ modes, activeMode, handleModeChange }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const theme = useTheme();
  const [selectedLabel, setSelectedLabel] = useState(
    modes.find((mode) => mode.id === activeMode)?.label || "בחר מצב"
  );

  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleSelect = (mode) => {
    handleModeChange(mode.id);
    setSelectedLabel(mode.label);
    handleClose();
  };

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        gap: "8px",
        justifyContent: "center",
      }}
    >
      <IconButton onClick={handleClick}>
        <MenuIcon />
      </IconButton>
      <Typography variant="body1" color={theme.palette.text.primary}>
        {selectedLabel}
      </Typography>
      <Menu
        sx={{
          direction: "ltr",
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        keepMounted
      >
        {modes.map((mode) => (
          <MenuItem
            key={mode.id}
            selected={activeMode === mode.id}
            onClick={() => handleSelect(mode)}
          >
            {mode.label}
          </MenuItem>
        ))}
      </Menu>
    </div>
  );
};

export default function Adam() {
  const [activeMode, setActiveMode] = useState("regular");
  const [enhancedMode, setEnhancedMode] = useState(false);
  const [openGoalsDialog, setOpenGoalsDialog] = useState(false);
  const [inputText, setInputText] = useState("");
  const [outputText, setOutputText] = useState("");
  const handleModeChange = (mode) => setActiveMode(mode);
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const timer = React.useRef(undefined);
  const isMobile = useMediaQuery("(max-width:1000px)");
  const [humanScore, setHumanScore] = useState(0);
  const buttonSx = {
    ...(success && {
      bgcolor: blue[500],
      "&:hover": {
        bgcolor: blue[700],
      },
    }),
  };

  useEffect(() => {
    const analyzeHumanText = async () => {
      try {
        const response = await AnalyzeText({ text: inputText });
        setHumanScore(response.data.score);
      } catch (error) {
        console.log(error);
      }
    };
    analyzeHumanText();
  }, [inputText]);
  const [settingsAnchorEl, setSettingsAnchorEl] = useState(null);

  const handleSettingsMenuClick = (event) => {
    setSettingsAnchorEl(event.currentTarget);
  };

  const handleSettingsMenuClose = () => {
    setSettingsAnchorEl(null);
  };

  React.useEffect(() => {
    return () => {
      clearTimeout(timer.current);
    };
  }, []);

  const handleButtonClick = () => {
    if (!loading) {
      setSuccess(false);
      setLoading(true);
      timer.current = setTimeout(() => {
        setSuccess(true);
        setLoading(false);
      }, 2000);
    }
  };

  const modes = useMemo(
    () => [
      { id: "regular", icon: "📄", label: "רגיל" },
      { id: "short", icon: "↔️", label: "קצר" },
      { id: "expand", icon: "↔️", label: "הרחב" },
      { id: "simplify", icon: "↕️", label: "פשט" },
      { id: "improve", icon: "✍️", label: "שפר כתיבה" },
    ],
    []
  );

  const handleCopyText = () => {
    navigator.clipboard.writeText(outputText);
    // אפשר להוסיף פה התראה על העתקה מוצלחת
  };

  const [goals, setGoals] = useState({
    contentFocus: "Informational",
    engagementLevel: "Medium",
    tone: "Neutral",
    clarity: "Clear",
    complexity: "Simple",
  });

  const handleGoalChange = (key, value) => {
    setGoals((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const handleGoalSave = () => {
    console.log(goals);
    setOpenGoalsDialog(false);
  };

  const handleDownload = () => {
    const element = document.createElement("a");
    const file = new Blob([outputText], { type: "text/plain" });
    element.href = URL.createObjectURL(file);
    element.download = "humanized-text.txt";
    document.body.appendChild(element);
    element.click();
    document.body.removeChild(element);
  };

  const getWordCount = (text) => {
    return text.trim().split(/\s+/).filter(Boolean).length;
  };

  // מצבים לטאבים במובייל
  const [tabValue, setTabValue] = useState(0);

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  return (
    <>
      <StyledPaper>
        <Box display="flex" justifyContent="space-between" mb={3}>
          {/* תפריט המצב */}
          {isMobile ? (
            <DropdownMenu
              modes={modes}
              activeMode={activeMode}
              handleModeChange={handleModeChange}
            />
          ) : (
            <Stack direction="row" spacing={1}>
              {modes.map((mode) => (
                <TopButton
                  key={mode.id}
                  active={activeMode === mode.id}
                  onClick={() => handleModeChange(mode.id)}
                  startIcon={<span>{mode.icon}</span>}
                >
                  {mode.label}
                </TopButton>
              ))}
            </Stack>
          )}

          {/* הגדרות */}
          {isMobile ? (
            <Box>
              <ActionButton
                onClick={handleSettingsMenuClick}
                variant="outlined"
              >
                ⚙️ תפריט הגדרות
              </ActionButton>
              <Menu
                sx={{ direction: "ltr" }}
                anchorEl={settingsAnchorEl}
                open={Boolean(settingsAnchorEl)}
                onClose={handleSettingsMenuClose}
              >
                <MenuItem>
                  <Box
                    sx={{
                      color: "#8f9199",
                      fontSize: "14px",
                      marginRight: "8px",
                    }}
                  >
                    מצב משופר
                  </Box>
                  <CustomSwitch
                    checked={enhancedMode}
                    onChange={(e) => setEnhancedMode(e.target.checked)}
                  />
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    setOpenGoalsDialog(true);
                    handleSettingsMenuClose();
                  }}
                >
                  <span style={{ marginRight: "8px" }}>✨</span> התאמה אישית
                </MenuItem>
                <MenuItem disabled>
                  <span style={{ marginRight: "8px" }}>🎯</span> מטרות
                </MenuItem>
              </Menu>
            </Box>
          ) : (
            <Stack direction="row" spacing={2} alignItems="center">
              <Tooltip title="שיפור איכות הטקסט">
                <Box sx={{ color: "#8f9199", fontSize: "14px" }}>מצב משופר</Box>
              </Tooltip>
              <CustomSwitch
                checked={enhancedMode}
                onChange={(e) => setEnhancedMode(e.target.checked)}
              />
              <Tooltip title="התאמה אישית של הטקסט">
                <ActionButton
                  onClick={() => setOpenGoalsDialog(true)}
                  variant="outlined"
                >
                  <span style={{ marginLeft: "4px" }}>✨</span>
                  התאמה אישית
                </ActionButton>
              </Tooltip>
              <Tooltip title="הגדרת מטרות">
                <ActionButton disabled variant="outlined">
                  <span style={{ marginLeft: "4px" }}>🎯</span>
                  מטרות
                </ActionButton>
              </Tooltip>
            </Stack>
          )}
        </Box>

        {/* תצוגת הטקסטים */}
        <Box
          flexGrow={1}
          display={{ xs: "block", md: "flex" }}
          gap={3}
          sx={{ minHeight: "400px" }}
        >
          {isMobile ? (
            <Box sx={{ width: "100%", margin: "0 auto" }}>
              {/* טאבים למובייל */}
              <Tabs
                value={tabValue}
                onChange={handleTabChange}
                indicatorColor="primary"
                textColor="primary"
                variant="fullWidth"
              >
                <Tab label="קלט" />
                <Tab label="תשובה" />
              </Tabs>
              <Box mt={2}>
                {tabValue === 0 && (
                  <TextArea
                    value={inputText}
                    humanScore={humanScore}
                    onChange={(e) => setInputText(e.target.value)}
                    placeholder="הכנס או הדבק את הטקסט שלך כאן..."
                    wordCount={getWordCount(inputText)}
                  />
                )}
                {tabValue === 1 && (
                  <TextArea
                    value={outputText}
                    setVao
                    readOnly
                    placeholder="הטקסט האנושי יופיע כאן..."
                    wordCount={getWordCount(outputText)}
                  />
                )}
              </Box>
            </Box>
          ) : (
            <>
              <TextArea
                value={inputText}
                humanScore={humanScore}
                onChange={(e) => setInputText(e.target.value)}
                placeholder="הכנס או הדבק את הטקסט שלך כאן..."
                wordCount={getWordCount(inputText)}
              />

              <Box display="flex" alignItems="center">
                <Box sx={{ m: 1, position: "relative" }}>
                  <Button
                    variant="contained"
                    sx={buttonSx}
                    disabled={loading}
                    onClick={handleButtonClick}
                  >
                    הרץ את ADAM
                  </Button>
                  {loading && (
                    <CircularProgress
                      size={24}
                      sx={{
                        color: blue[500],
                        position: "absolute",
                        top: "50%",
                        left: "50%",
                        marginTop: "-12px",
                        marginLeft: "-12px",
                      }}
                    />
                  )}
                </Box>
              </Box>

              <TextArea
                value={outputText}
                setVao
                readOnly
                placeholder="הטקסט האנושי יופיע כאן..."
                wordCount={getWordCount(outputText)}
              />
            </>
          )}
        </Box>

        {isMobile && (
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Box sx={{ m: 1, position: "relative" }}>
              <Button
                variant="contained"
                sx={buttonSx}
                disabled={loading}
                onClick={handleButtonClick}
              >
                הרץ את ADAM
              </Button>
              {loading && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: blue[500],
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    marginTop: "-12px",
                    marginLeft: "-12px",
                  }}
                />
              )}
            </Box>
          </Box>
        )}

        {/* כפתורי פעולה בתחתית */}
        <Box
          display="flex"
          sx={{
            direction: "rtl",
            justifyContent: isMobile ? "center" : "flex-start",
            gap: 1,
            alignItems: "center",
          }}
          alignItems="center"
          mt={2}
        >
          <Tooltip title="אהבתי">
            <IconButton size="small" sx={{ color: "#8f9199" }}>
              <ThumbUpIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="לא אהבתי">
            <IconButton size="small" sx={{ color: "#8f9199" }}>
              <ThumbDownIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="הורד קובץ">
            <IconButton
              size="small"
              sx={{ color: "#8f9199" }}
              onClick={handleDownload}
            >
              <DownloadIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="העתק לקליפבורד">
            <IconButton
              size="small"
              sx={{ color: "#8f9199" }}
              onClick={handleCopyText}
            >
              <ContentCopyIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </StyledPaper>

      {/* GoalsDialog */}
      <GoalsDialog
        open={openGoalsDialog}
        onClose={() => setOpenGoalsDialog(false)}
        onSave={handleGoalSave}
      />
    </>
  );
}
