import React from "react";
import { Box, Stack, TextField, Typography } from "@mui/material";
import { alpha, styled } from "@mui/material/styles";
import { useTheme } from "@emotion/react";

const StyledTextField = styled(TextField)(({ theme }) => ({
  width: "100%",
  height: "100%",
  direction: "ltr", // ltr כברירת מחדל
  "& .MuiOutlinedInput-root": {
    height: "100%",
    color: alpha(theme.palette.text.primary, 0.8),
    backgroundColor: theme.vars
      ? theme.vars.palette.background.default
      : "#0f1117",
    fontFamily: "Assistant, Rubik, Arial, sans-serif",
    fontSize: "15px",
    lineHeight: "1.5",
    direction: "ltr", // ltr לאלמנט הקלט עצמו
    "& fieldset": {
      borderColor: "#1e2029",
      borderRadius: "12px",
      transition: "all 0.2s ease",
    },
    "&:hover fieldset": {
      borderColor: "#2e2f39",
    },
    "&.Mui-focused fieldset": {
      borderColor: "#7c3aed",
      borderWidth: "2px",
    },
    "& textarea": {
      direction: "ltr",
      padding: "16px",
      caretColor: "#7c3aed", // צבע הסמן
      "&::placeholder": {
        color: "#8f9199",
        opacity: 1,
        textAlign: "left",
      },
    },
    "& .MuiOutlinedInput-input": {
      textAlign: "left",
      "&::selection": {
        backgroundColor: theme.vars
          ? theme.vars.palette.background.default
          : "#0f1117",
      },
    },
  },
  "& .MuiInputBase-input.Mui-disabled": {
    WebkitTextFillColor: "#8f9199",
    backgroundColor: theme.vars
      ? theme.vars.palette.background.default
      : "#0f1117",
    opacity: 0.7,
  },
  // הוספת סקרולבר מעוצב
  "& textarea::-webkit-scrollbar": {
    width: "8px",
  },
  "& textarea::-webkit-scrollbar-track": {
    background: "#1e2029",
    borderRadius: "4px",
  },
  "& textarea::-webkit-scrollbar-thumb": {
    background: "#2e2f39",
    borderRadius: "4px",
    "&:hover": {
      background: "#3e3f49",
    },
  },
}));

const StatsContainer = styled(Box)(({ theme }) => ({
  display: "flex",
  justifyContent: "flex-start", // שינוי ליישור ימין
  alignItems: "center",
  marginTop: "12px",
  direction: "ltr", // ltr למיכל הסטטיסטיקות
  color: alpha(theme.palette.text.primary, 0.8),
}));

// Styled components
const StatItem = styled(Box)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  gap: "8px",
  color: alpha(theme.palette.text.primary, 0.8),
  fontSize: "14px",
  padding: "4px 8px",
  borderRadius: "6px",
  transition: "background-color 0.2s ease",
  "&:hover": {
    backgroundColor: "rgba(124, 58, 237, 0.1)",
  },
  "& .label": {
    color: alpha(theme.palette.text.primary, 0.8),
  },
  "& .value": {
    color: alpha(theme.palette.text.primary, 0.8),
    fontWeight: 500,
    minWidth: "20px",
    textAlign: "center",
  },
}));

const TextArea = ({
  value = "",
  onChange,
  placeholder,
  readOnly = false,
  disabled = false,
  minRows = 20,
  maxRows = 50,
  showWordCount = true,
  showHumanScore = true,
  humanScore,
  error,
  helperText,
  className,
  style,
  onFocus,
  onBlur,
  onKeyDown,
  isAi = false,
}) => {
  const theme = useTheme();
  const wordCount = React.useMemo(() => {
    return value?.trim()
      ? value
          .trim()
          .split(/\s+/)
          .filter((word) => word.length > 0).length
      : 0;
  }, [value]);

  const charCount = React.useMemo(() => {
    return value?.length || 0;
  }, [value]);

  const handleChange = React.useCallback(
    (e) => {
      if (onChange) {
        onChange(e);
      }
    },
    [onChange]
  );

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        flex: 1,
        height: "100%",
        position: "relative",
      }}
    >
      <StyledTextField
        multiline
        minRows={minRows}
        maxRows={maxRows}
        value={value}
        onChange={handleChange}
        placeholder={placeholder}
        disabled={disabled}
        onFocus={onFocus}
        onBlur={onBlur}
        onKeyDown={onKeyDown}
        Input={{
          readOnly: readOnly,
          sx: {
            ...(readOnly && {
              backgroundColor: theme.vars
                ? theme.vars.palette.background.default
                : "#0f1117",
              cursor: "default",
              opacity: 0.9,
            }),
          },
        }}
        error={error}
        helperText={helperText}
        className={className}
        style={style}
      />

      {(showWordCount || showHumanScore) && (
        <StatsContainer>
          <Stack direction="row" spacing={2}>
            {showWordCount && (
              <>
                <StatItem>
                  <span className="label">מילים:</span>
                  <span className="value">
                    {wordCount.toLocaleString("he-IL")}
                  </span>
                </StatItem>
                <StatItem>
                  <span className="label">תווים:</span>
                  <span className="value">
                    {charCount.toLocaleString("he-IL")}
                  </span>
                </StatItem>
              </>
            )}
            {showHumanScore && (
              <StatItem>
                <span className="label">ציון אנושיות:</span>
                <span className="value">{humanScore || "-"}</span>
              </StatItem>
            )}
          </Stack>
        </StatsContainer>
      )}
    </Box>
  );
};

export default React.memo(TextArea);
