import React from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  Box,
  Button,
  Typography,
  IconButton,
  styled,
  alpha,
  Divider,
  useMediaQuery,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import { useTheme } from "@emotion/react";

// Styled components
const StyledDialog = styled(Dialog)(({ theme }) => ({
  "& .MuiDialog-paper": {
    color: alpha(theme.palette.text.primary, 0.8),
    backgroundColor: theme.vars
      ? theme.vars.palette.background.default
      : "#0f1117",
    maxWidth: 600,
    width: "100%",
    borderRadius: 12,
  },
}));

const OptionButton = styled(Button)(({ selected, theme }) => ({
  color: theme.palette.text.primary,
  backgroundImage:
    theme.palette.mode === "light"
      ? "linear-gradient(18deg, #eee, #ddd)"
      : "linear-gradient(18deg, #7c3aed, #7c3aaa)",
  border: "1px solid #2e2f39",
  borderRadius: 20,
  borderWidth: selected ? 2 : 1,
  borderColor: selected
    ? theme.palette.mode === "light"
      ? "#7c3aed"
      : "#fff"
    : "#2e2f39",
  padding: "6px 16px",
  textTransform: "none",
  "&:hover": {
    color: alpha(theme.palette.text.primary, 0.8),
    backgroundColor: selected ? "#7c3aed" : "rgba(255, 255, 255, 0.05)",
  },
}));

const CategoryTitle = styled(Typography)(({ theme }) => ({
  color: alpha(theme.palette.text.primary, 0.8),
  fontSize: "16px",
  fontWeight: "bold",

  marginBottom: "12px",
}));

const CategoryDescription = styled(Typography)(({ theme }) => ({
  color: alpha(theme.palette.text.primary, 0.8),
  fontSize: "14px",
  marginTop: "8px",
}));

// קבועים לשימוש בקומפוננטה
const INITIAL_STATE = {
  contentFocus: "אינפורמטיבי",
  engagementLevel: "בינוני",
  tone: "ניטרלי",
  clarity: "ברור",
  complexity: "פשוט",
};

const GoalsDialog = ({ open, onClose, onSave, initialData = null }) => {
  // סטייט מרכזי לכל ההגדרות
  const [settings, setSettings] = React.useState(initialData || INITIAL_STATE);
  const [isLoading, setIsLoading] = React.useState(false);
  const [error, setError] = React.useState(null);
  const theme = useTheme();
  const isMobile = useMediaQuery("(max-width:1000px)");
  // פונקציה לעדכון הגדרה בודדת
  const updateSetting = (key, value) => {
    setSettings((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  // טיפול בשמירת ההגדרות
  const handleSave = async () => {
    try {
      setIsLoading(true);
      setError(null);

      // כאן יהיה הקריאה ל-API
      // await saveGoalsSettings(settings);

      if (onSave) {
        await onSave(settings);
      }

      onClose();
    } catch (err) {
      setError("אירעה שגיאה בשמירת ההגדרות");
      console.error("Error saving goals:", err);
    } finally {
      setIsLoading(false);
    }
  };

  // איפוס הגדרות
  const handleReset = () => {
    setSettings(INITIAL_STATE);
  };

  return (
    <StyledDialog
      sx={{
        direction: "ltr",
      }}
      open={open}
      onClose={onClose}
      fullWidth
      fullScreen={isMobile}
    >
      <DialogTitle sx={{ borderBottom: "1px solid #1e2029", p: 2 }}>
        <Box display="flex" alignItems="center" justifyContent="space-between">
          <Typography
            variant="h6"
            sx={{
              color: alpha(theme.palette.text.primary, 0.8),
              fontWeight: 500,
            }}
          >
            הגדרת מטרות
          </Typography>
          <IconButton
            onClick={onClose}
            sx={{ color: alpha(theme.palette.text.primary, 0.8) }}
          >
            <CloseIcon />
          </IconButton>
        </Box>
      </DialogTitle>

      <DialogContent sx={{ p: 3 }}>
        {/* Navigation Buttons */}
        <Box sx={{ display: "flex", gap: 2, mb: 4 }}>
          {["סטנדרטי", "קצר", "הרחב", "פשט", "שפר כתיבה"].map((item) => (
            <Button
              key={item}
              sx={{
                color:
                  item === "סטנדרטי"
                    ? alpha(theme.palette.text.primary, 0.8)
                    : "#8f9199",
                textTransform: "none",
                borderBottom: item === "סטנדרטי" ? "2px solid #7c3aed" : "none",
                borderRadius: 0,
                px: 1,
              }}
            >
              {item}
            </Button>
          ))}
        </Box>
        {/* Content Focus */}
        <Box sx={{ mb: 4 }}>
          <CategoryTitle>מיקוד תוכן</CategoryTitle>
          <Box sx={{ display: "flex", gap: 1, flexWrap: "wrap" }}>
            {["אינפורמטיבי", "אנליטי", "משכנע", "סיפורי"].map((item) => (
              <OptionButton
                key={item}
                selected={settings.contentFocus === item}
                onClick={() => updateSetting("contentFocus", item)}
              >
                {item}
              </OptionButton>
            ))}
          </Box>
          <CategoryDescription>
            מייצר תוכן המתמקד בעיקר בהעברת מידע
          </CategoryDescription>
        </Box>
        <Divider
          sx={{
            backgroundColor:
              theme.palette.mode === "light" ? "#7c3aed" : "#3b82f6",
            marginBottom: "10px !important",
          }}
        />
        {/* Engagement Level */}
        <Box sx={{ mb: 4 }}>
          <CategoryTitle>רמת מעורבות</CategoryTitle>
          <Box sx={{ display: "flex", gap: 1 }}>
            {["נמוך", "בינוני", "גבוה"].map((item) => (
              <OptionButton
                key={item}
                selected={settings.engagementLevel === item}
                onClick={() => updateSetting("engagementLevel", item)}
              >
                {item}
              </OptionButton>
            ))}
          </Box>
          <CategoryDescription>
            מייצר תוכן עם מעורבות מתונה, מאזן בין מידע ועניין הקורא
          </CategoryDescription>
        </Box>
        <Divider
          sx={{
            backgroundColor:
              theme.palette.mode === "light" ? "#7c3aed" : "#3b82f6",
            marginBottom: "10px !important",
          }}
        />
        {/* Tone */}
        <Box sx={{ mb: 4 }}>
          <CategoryTitle>טון</CategoryTitle>
          <Box sx={{ display: "flex", gap: 1 }}>
            {["פורמלי", "ניטרלי", "לא פורמלי"].map((item) => (
              <OptionButton
                key={item}
                selected={settings.tone === item}
                onClick={() => updateSetting("tone", item)}
              >
                {item}
              </OptionButton>
            ))}
          </Box>
          <CategoryDescription>שומר על טון ניטרלי</CategoryDescription>
        </Box>
        <Divider
          sx={{
            backgroundColor:
              theme.palette.mode === "light" ? "#7c3aed" : "#3b82f6",
            marginBottom: "10px !important",
          }}
        />
        {/* Clarity */}
        <Box sx={{ mb: 4 }}>
          <CategoryTitle>בהירות</CategoryTitle>
          <Box sx={{ display: "flex", gap: 1 }}>
            {["ברור", "תמציתי", "מפורט"].map((item) => (
              <OptionButton
                key={item}
                selected={settings.clarity === item}
                onClick={() => updateSetting("clarity", item)}
              >
                {item}
              </OptionButton>
            ))}
          </Box>
          <CategoryDescription>
            מייצר תוכן עם דגש על בהירות, נמנע מעמימות ובלבול
          </CategoryDescription>
        </Box>
        <Divider
          sx={{
            backgroundColor:
              theme.palette.mode === "light" ? "#7c3aed" : "#3b82f6",
            marginBottom: "10px !important",
          }}
        />
        {/* Language Complexity */}
        <Box sx={{ mb: 4 }}>
          <CategoryTitle>מורכבות שפה</CategoryTitle>
          <Box sx={{ display: "flex", gap: 1 }}>
            {["פשוט", "בינוני", "מורכב"].map((item) => (
              <OptionButton
                key={item}
                selected={settings.complexity === item}
                onClick={() => updateSetting("complexity", item)}
              >
                {item}
              </OptionButton>
            ))}
          </Box>
          <CategoryDescription>
            משתמש בשפה ומבני משפטים פשוטים המתאימים להבנה קלה
          </CategoryDescription>
        </Box>
        {/* Error Message */}
        {error && (
          <Typography color="error" sx={{ mb: 2 }}>
            {error}
          </Typography>
        )}
        <Divider
          sx={{
            backgroundColor:
              theme.palette.mode === "light" ? "#7c3aed" : "#3b82f6",
            marginBottom: "10px !important",
          }}
        />
        {/* Buttons */}
        <Box
          sx={{
            display: "flex",
            justifyContent: "flex-start",
            direction: "ltr",
            gap: 2,
            mt: 2,
            mb: 5,
          }}
        >
          <Button
            variant="outlined"
            disabled={isLoading}
            onClick={handleSave}
            sx={{
              backgroundColor:
                theme.palette.mode === "light" ? "#7c3aed" : "#3b82f6",
              textTransform: "none",
              "&:hover": {
                backgroundColor: theme.vars
                  ? `rgba(${theme.vars.palette.background.defaultChannel} / 0.4)`
                  : alpha(theme.palette.background.default, 0.4),
              },
            }}
          >
            <Typography
              color={theme.palette.mode === "light" ? "#fff" : "#fff"}
            >
              {isLoading ? "שומר..." : "שמור"}
            </Typography>
          </Button>
          <Button
            variant="outlined"
            onClick={handleReset}
            sx={{
              color: alpha(theme.palette.text.primary, 0.8),
              borderColor: "#2e2f39",
              textTransform: "none",
            }}
          >
            אפס
          </Button>
        </Box>
      </DialogContent>
    </StyledDialog>
  );
};

export default GoalsDialog;
