const TypewriterDemo = () => {
  const [currentText, setCurrentText] = React.useState({ ai: "", human: "" });
  const [currentChar, setCurrentChar] = React.useState(0);
  const [currentIndex, setCurrentIndex] = React.useState(0);
  const [isErasing, setIsErasing] = React.useState(false);

  React.useEffect(() => {
    const text = demoTexts[currentIndex];

    if (isErasing) {
      if (currentText.ai.length > 0) {
        const timeout = setTimeout(() => {
          setCurrentText({
            ai: currentText.ai.slice(0, -1),
            human: currentText.human.slice(0, -1),
          });
        }, 30); // מהירות מחיקה

        return () => clearTimeout(timeout);
      } else {
        setIsErasing(false);
        setCurrentChar(0);
        setCurrentIndex((prev) => (prev + 1) % demoTexts.length);
      }
    } else if (currentChar < Math.max(text.ai.length, text.human.length)) {
      const timeout = setTimeout(() => {
        setCurrentText({
          ai: text.ai.slice(0, currentChar + 1),
          human: text.human.slice(0, currentChar + 1),
        });
        setCurrentChar((prev) => prev + 1);
      }, 50); // מהירות הקלדה

      return () => clearTimeout(timeout);
    } else {
      const timeout = setTimeout(() => {
        setIsErasing(true);
      }, 2000); // זמן המתנה לפני מחיקה

      return () => clearTimeout(timeout);
    }
  }, [currentChar, currentIndex, isErasing, currentText]);

  return (
    <Stack
      spacing={4}
      sx={{ width: "100%", maxWidth: { xs: "100%", md: "800px" } }}
    >
      <TextCard type="ai">
        <Typography variant="h6" color="primary" sx={{ mb: 1 }}>
          טקסט AI - {demoTexts[currentIndex].category}
        </Typography>
        <TypewriterText
          variant="h6"
          className={
            !isErasing &&
            currentText.ai.length < demoTexts[currentIndex].ai.length
              ? "typing"
              : ""
          }
          sx={{
            color: "text.primary",
            minHeight: { xs: "80px", md: "60px" },
            direction: "ltr",
          }}
        >
          {currentText.ai}
        </TypewriterText>
      </TextCard>

      <Box sx={{ display: "flex", justifyContent: "center" }}>
        <TransformationArrow />
      </Box>

      <TextCard type="human">
        <Typography variant="h6" color="success.main" sx={{ mb: 1 }}>
          טקסט אנושי - {demoTexts[currentIndex].category}
        </Typography>
        <TypewriterText
          variant="h6"
          className={
            !isErasing &&
            currentText.human.length < demoTexts[currentIndex].human.length
              ? "typing"
              : ""
          }
          sx={{
            color: "text.primary",
            fontFamily: "system-ui",
            minHeight: { xs: "80px", md: "60px" },
            direction: "ltr",
          }}
        >
          {currentText.human}
        </TypewriterText>
      </TextCard>
    </Stack>
  );
};
export default function SignIn(props) {
  const navigate = useNavigate();
  const theme = useTheme();
  const isMobile = useMediaQuery("(max-width:1000px)");
  const [mode, setMode] = React.useState(null);
  const [logo, setLogo] = React.useState(adamDarkMode);
  // we need to check the value of mode on local storage key called mui-mode with value dark or light or system
  React.useEffect(() => {
    const mode = localStorage.getItem("mui-mode");
    if (mode) {
      setMode(mode);
      setLogo(mode === "dark" ? adamDarkMode : adamWhiteMode);
    }
  }, [mode]);

  return (
    <AppTheme {...props}>
      <CssBaseline />
      <SignInContainer isMobile={isMobile}>
        <ColorModeSelect
          setModeColor={setMode}
          sx={{
            position: "fixed",
            top: "1rem",
            right: "1rem",
            zIndex: 2,
          }}
        />

        <LoginSection isMobile={isMobile}>
          <Stack
            spacing={4}
            alignItems="center"
            maxWidth="400px"
            sx={{ position: "relative", zIndex: 1 }}
          >
            <Box
              sx={{
                textAlign: "center",
              }}
            >
              <AutoFixHighIcon
                sx={{
                  fontSize: 48,
                  color: "primary.main",
                  mb: 2,
                  animation: "bounce 2s infinite",
                }}
              />
              {/* <Typography
                variant="h3"
                sx={{
                  fontWeight: 800,
                  mb: 2,
                  background: "linear-gradient(45deg, #7c3aed, #3b82f6)",
                  WebkitBackgroundClip: "text",
                  WebkitTextFillColor: "transparent",
                }}
              >
                ADAM AI
              </Typography> */}

              {/* we need add svg logo adam-dark-mode.svg */}

              <img
                style={{
                  width: isMobile ? "50%" : "90%",
                }}
                src={logo}
                alt="adam logo"
              />
              {/* <img src={adamDarkMode} alt="adam logo" /> */}
              {/* <img src={adamWhiteMode} alt="adam logo" /> */}

              <Typography
                variant="h6"
                sx={{
                  color: "text.secondary",
                  fontWeight: 500,
                  mb: 4,
                }}
              >
                הפוך טקסט AI לאנושי בקלות
              </Typography>
            </Box>

            <GlowingButton
              onClick={() => navigate("/")}
              fullWidth
              startIcon={<GoogleIcon />}
            >
              התחבר עם Google
            </GlowingButton>

            <Typography
              variant="caption"
              sx={{
                color: "text.secondary",
                textAlign: "center",
                mt: 2,
              }}
            >
              כניסה מאובטחת באמצעות Google
            </Typography>
          </Stack>
        </LoginSection>
        {!isMobile && (
          <>
            <DemoSection>
              <TypewriterDemo />
            </DemoSection>
            {/* רקע מונפש */}
            <Box
              sx={{
                position: "absolute",
                width: "100%",
                height: "100%",
                top: 0,
                left: 0,
                background:
                  "radial-gradient(circle at 50% 50%, rgba(124, 58, 237, 0.1) 0%, transparent 50%)",
                opacity: 0.6,
                zIndex: 0,
                animation: "pulse 15s infinite",
                pointerEvents: "none",
              }}
            />
          </>
        )}
      </SignInContainer>
    </AppTheme>
  );
}

