import * as React from "react";
import { styled, alpha } from "@mui/material/styles";
import Box from "@mui/material/Box";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import Container from "@mui/material/Container";
import Divider from "@mui/material/Divider";
import MenuItem from "@mui/material/MenuItem";
import Drawer from "@mui/material/Drawer";
import MenuIcon from "@mui/icons-material/Menu";
import CloseRoundedIcon from "@mui/icons-material/CloseRounded";
import AutoFixHighIcon from "@mui/icons-material/AutoFixHigh";
import { Typography, useMediaQuery } from "@mui/material";
import ColorModeIconDropdown from "../shared-theme/ColorModeIconDropdown";
import { Navigate, useNavigate } from "react-router-dom";
import AdamDarkMode from "../adam-dark-mode.svg";
import AdamWhiteMode from "../adam-white-mode.svg";
import { useTheme } from "@emotion/react";
const StyledToolbar = styled(Toolbar)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
  flexShrink: 0,
  borderRadius: `calc(${theme.shape.borderRadius}px + 8px)`,
  backdropFilter: "blur(24px)",
  border: "1px solid",
  borderColor: (theme.vars || theme).palette.divider,
  backgroundColor: theme.vars
    ? `rgba(${theme.vars.palette.background.defaultChannel} / 0.4)`
    : alpha(theme.palette.background.default, 0.4),
  boxShadow: (theme.vars || theme).shadows[1],
  padding: "8px 12px",
}));

const menuItems = [
  { text: "יתרונות", href: "#features" },
  { text: "איך זה עובד", href: "#how-it-works" },
  { text: "מחירים", href: "#pricing" },
  { text: "המלצות", href: "#testimonials" },
];

export default function AppAppBar() {
  const [open, setOpen] = React.useState(false);
  const navigate = useNavigate();
  const toggleDrawer = (newOpen) => () => {
    setOpen(newOpen);
  };

  const theme = useTheme();
  const isMobile = useMediaQuery("(max-width:1000px)");
  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
    }
    setOpen(false);
  };

  return (
    <AppBar
      position="fixed"
      enableColorOnDark
      sx={{
        boxShadow: 0,
        bgcolor: "transparent",
        backgroundImage: "none",
        mt: "calc(var(--template-frame-height, 0px) + 28px)",
      }}
    >
      <Container maxWidth="lg">
        <StyledToolbar variant="dense" disableGutters>
          <Box
            sx={{ flexGrow: 1, display: "flex", alignItems: "center", gap: 2 }}
          >
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              {theme.palette.mode === "dark" ? (
                <img
                  style={{ width: isMobile ? "100px" : "150px" }}
                  src={AdamDarkMode}
                  alt="Adam AI"
                />
              ) : (
                <img
                  style={{ width: isMobile ? "100px" : "150px" }}
                  src={AdamWhiteMode}
                  alt="Adam AI"
                />
              )}
            </Box>
            <Box sx={{ display: { xs: "none", md: "flex" }, gap: 1 }}>
              {menuItems.map((item) => (
                <Button
                  key={item.text}
                  variant="text"
                  color="primary"
                  size="small"
                  onClick={() => scrollToSection(item.href.slice(1))}
                  sx={{
                    fontSize: "0.9rem",
                    "&:hover": {
                      backgroundColor: "rgba(124, 58, 237, 0.08)",
                    },
                  }}
                >
                  {item.text}
                </Button>
              ))}
            </Box>
          </Box>
          <Box
            sx={{
              display: { xs: "none", md: "flex" },
              gap: 2,
              alignItems: "center",
            }}
          >
            <Button
              color="primary"
              variant="text"
              size="small"
              sx={{
                fontWeight: 500,
                "&:hover": {
                  backgroundColor: "rgba(124, 58, 237, 0.08)",
                },
              }}
            >
              התחברות
            </Button>
            <Button
              color="primary"
              variant="contained"
              size="small"
              sx={{
                px: 3,
                borderRadius: "20px",
                background: "linear-gradient(45deg, #7c3aed, #3b82f6)",
                "&:hover": {
                  background: "linear-gradient(45deg, #6d31d8, #2563eb)",
                },
              }}
            >
              נסה בחינם
            </Button>
            <ColorModeIconDropdown />
          </Box>
          <Box sx={{ display: { xs: "flex", md: "none" }, gap: 1 }}>
            <ColorModeIconDropdown size="medium" />
            <IconButton
              aria-label="Menu button"
              onClick={toggleDrawer(true)}
              sx={{ color: "primary.main" }}
            >
              <MenuIcon />
            </IconButton>
            <Drawer
              anchor="top"
              open={open}
              onClose={toggleDrawer(false)}
              PaperProps={{
                sx: {
                  direction: "ltr",
                  textAlign: "left",
                  top: "var(--template-frame-height, 0px)",
                },
              }}
            >
              <Box sx={{ p: 2, backgroundColor: "background.default" }}>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                    mb: 2,
                  }}
                >
                  <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
                    <AutoFixHighIcon sx={{ color: "primary.main" }} />
                    <Typography variant="h6" sx={{ fontWeight: "bold" }}>
                      ADAM AI
                    </Typography>
                  </Box>
                  <IconButton onClick={toggleDrawer(false)}>
                    <CloseRoundedIcon />
                  </IconButton>
                </Box>

                {menuItems.map((item) => (
                  <MenuItem
                    key={item.text}
                    onClick={() => scrollToSection(item.href.slice(1))}
                    sx={{
                      py: 1.5,
                      borderRadius: 1,
                      "&:hover": {
                        backgroundColor: "rgba(124, 58, 237, 0.08)",
                      },
                    }}
                  >
                    {item.text}
                  </MenuItem>
                ))}
                <Divider sx={{ my: 3 }} />
                <MenuItem>
                  <Button
                    color="primary"
                    variant="contained"
                    fullWidth
                    sx={{
                      py: 1,
                      borderRadius: "20px",
                      background: "linear-gradient(45deg, #7c3aed, #3b82f6)",
                    }}
                  >
                    נסה בחינם
                  </Button>
                </MenuItem>
                <MenuItem>
                  <Button
                    onClick={() => navigate("/sign-in")}
                    color="primary"
                    variant="outlined"
                    fullWidth
                    sx={{
                      py: 1,
                      borderRadius: "20px",
                    }}
                  >
                    התחברות
                  </Button>
                </MenuItem>
              </Box>
            </Drawer>
          </Box>
        </StyledToolbar>
      </Container>
    </AppBar>
  );
}
